.ProductList {
  margin-top: 20px;
}
.ProductList-Products {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
}

.ProductList .icon {
  width: 50px;
  height: 50px;
  margin: 0 50px;
}

.IconHelp {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: -50px;
}

.Iconx {
  width: 50px;
  height: 50px;
  margin: 0 50px;
}

.ProductList .zoom {
  padding: 70px;
  -webkit-transition: -webkit-transform .4s;
  transition: -webkit-transform .4s;
  -o-transition: transform .4s;
  transition: transform .4s;
  transition: transform .4s, -webkit-transform .4s; /* Animation */
  width: 200px;
  height: 200px;
  margin: 0 auto;
  margin-bottom: -53px;
}

.zoom:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}



